'use client';
import React from 'react';
import data from './schema.json';

export default function PortfolioPage() {
  // Always check if data exists to prevent crashes
  if (!data || !data.sections) return null;

  const { theme, sections } = data;

  return (
    <div style={{ 
      backgroundColor: theme.darkMode ? '#121212' : '#ffffff', 
      color: theme.darkMode ? '#ffffff' : '#121212',
      minHeight: '100vh',
      padding: '40px',
      fontFamily: 'sans-serif'
    }}>
      {sections.map((section, index) => (
        <div key={index} style={{ marginBottom: '40px' }}>
          <h1 style={{ color: theme.primaryColor }}>{section.data.title}</h1>
          <p>{section.data.subtitle || section.data.text}</p>
        </div>
      ))}
    </div>
  );
}